	module.exports = {"data":{"postBySlug":{"html":"<p>Active Workspace is a Teamcenter Client that utilizes web technologies like node.js, angularjs and others to meet the needs of many engineering, manufacturing and other systems around the world.  With these technologies na modern web development we can extend the Active Workspace to automate tasks on the client or display data a certain way.</p>\n<p>To get started with this walkthrough make sure your <a href=\"https://activeworkspaceextensions.github.io/environment-setup\">environment is setup</a>, and if you were able to have <a href=\"https://activeworkspaceextensions.github.io/express-setup\">express running</a>.</p>\n<p>There are a few ways to create an extension within Active Workspace.  For this tutorial however we will go over the generator that comes with the client and how to generate a tools and info panel.</p>\n<p>Initialize the environment with initenv.cmd, this is located directly inside the Stage directory</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token function\">makeArray</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// Hey hey what can I do</span>\n    <span class=\"token keyword\">const</span> foo <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span>\n    bar<span class=\"token punctuation\">.</span><span class=\"token function\">split</span><span class=\"token punctuation\">(</span><span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span>letter <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n      foo<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span>letter<span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n    <span class=\"token keyword\">return</span> foo\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>","timeToRead":1,"excerpt":"Active Workspace is a Teamcenter Client that utilizes web technologies like node.js, angularjs and others to meet the needs of many…","frontmatter":{"title":"My First Active Workspace Extension","cover":null,"date":null,"category":null,"tags":null}},"tableOfContents":{"chapters":[{"title":"Setup","entries":[{"entry":{"id":"C:/Active_Workspace/Github-AW-Docs/1/content/lessons/Environment_Setup.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/environment-setup"},"frontmatter":{"title":"Environment Setup"}}}},{"entry":{"id":"C:/Active_Workspace/Github-AW-Docs/1/content/lessons/Express_Setup.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/express-setup"},"frontmatter":{"title":"Express Setup"}}}}]},{"title":"Get Started","entries":[{"entry":{"id":"C:/Active_Workspace/Github-AW-Docs/1/content/lessons/My_First_Extension.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/my-first-active-workspace-extension"},"frontmatter":{"title":"My First Active Workspace Extension"}}}},{"entry":{"id":"C:/Active_Workspace/Github-AW-Docs/1/content/lessons/lesson_two.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/lesson-two"},"frontmatter":{"title":"Lesson Two"}}}}]},{"title":"Examples","entries":[{"entry":{"id":"C:/Active_Workspace/Github-AW-Docs/1/content/lessons/Location_Example.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/location-example"},"frontmatter":{"title":"Location Example"}}}}]}]}},"pathContext":{"slug":"/my-first-active-workspace-extension"}}

